import { defineStore } from 'pinia'

// useStore could be anything like useUser, useCart
// the first argument is a unique id of the store across your application
import CONSTANTS from '@/constants'

export const useStore = defineStore('dndStore', {
  state() { 
    return {
      name: "",
      race: "",
      level: 1,
      strength: 10,
      dexterity: 10,
      constitution: 10,
      intelligence: 10,
      wisdom: 10,
      charisma: 10,
      hp: 10,
      maxhp: 20,
      ac: 10,
      initiative: 10,
      speed: 0,
      proficiencies: {
        'athletics': false,
        'acrobatics': false,
        'sleight of hand': false,
        'stealth': false,
        'arcana': false,
        'history': false,
        'investigation': false,
        'nature': false,
        'religion': false,
        'animal handling': false,
        'insight': false,
        'medicine': false,
        'perception': false,
        'survival': false,
        'deception': false,
        'intimidation': false,
        'performance': false,
        'persuasion': false,
        'strength_save': false,
        'dexterity_save': false,
        'constitution_save': false,
        'intelligence_save': false,
        'wisdom_save': false,
        'charisma_save': false,
      },
      columns: [
        [
          {type: "status1", id:1},
          {type: "status2", id:2},
          {type: "str", id:10},
          {type: "dex", id:11},
          {type: "con", id:12},
          {type: "int", id:13},
          {type: "wis", id:14},
          {type: "cha", id:15},
        ],
        [
          {type: "hp", id:7},
          {type: "text", id:4},
          {type: "text", id:5},
          {type: "bananas", id:6},
          {type: "attack", id:8},
          {type: "spellslots", id:9},
        ], [
          {type: "text", id:16},
          {type: "portrait", id:17},
        ]
      ],
      elementData: {
        4: {title: "Four", text: "--",},
        5: {title: "Five", text: "--",},
      },
      noSave: {
        dragging: false,
        characters: JSON.parse(localStorage.getItem('characters')) || {},
      }
    }
  },
  actions: {
    saveAsNew() {
      this.save(this.as_new_index)
    },
    save(slot) {
      let charData = Object.fromEntries(
        Object.entries(this).filter(
          ([key, ]) => {
            if (key.startsWith("$")) return false
            if (key.startsWith("_")) return false
            if (key == "noSave") return false
            return true
      }))
      let characters = JSON.parse(localStorage.getItem('characters'))
      if (characters == null)
        characters = {}
      characters[slot] = charData

      localStorage.setItem('characters', JSON.stringify(characters))
      this.refreshCharactersInformation()
    },
    load(slot) {
      let characters = JSON.parse(localStorage.getItem('characters'))
      let to_load = characters[slot]
      this.$patch(to_load)
    },
    delete(slot) {
      if (!confirm(`Delete ${this.noSave.characters[slot].name} in slot ${slot}?`))
        return // Abort early
      let characters = JSON.parse(localStorage.getItem('characters'))
      delete characters[slot]
      localStorage.setItem('characters', JSON.stringify(characters))
      this.refreshCharactersInformation()
    },
    refreshCharactersInformation() {
      this.noSave.characters = JSON.parse(localStorage.getItem('characters')) || {}
    },
    getModValue(stat) {
      return Math.floor(Number(stat)/2-5)
    },
    plusAndRound(mod_value) {
      const sign = mod_value >= 0 ? "+" : ""
      return sign + Math.floor(mod_value)
    },
    getSpellCount(spell_level) { 
      if (CONSTANTS.slots_by_level[this.level] != null)
        if (CONSTANTS.slots_by_level[this.level][spell_level] != null)
          return CONSTANTS.slots_by_level[this.level][spell_level]
      return undefined
    },
    addElement(column, type) {
      let largest = 0
      for (const col of this.columns) {
        for (const ele of col) {
          if (ele.id > largest)
            largest = ele.id
        }
      }
      console.log("Creating new "+type+" with id "+(largest+1))
      this.columns[column].push({
        type: type,
        id: largest+1,
      })
    },
    cleanElementData() {
      let all_ids = new Set()
      for (const col of this.columns) {
        for (const ele of col) {
          if (ele.id in all_ids)
            return console.error("ID "+ele.id+" APPEARS MULTIPLE TIMES")
          all_ids.add(ele.id)
        }
      }
      for (const id of Object.keys(this.elementData)) {
        if (!(all_ids.has(Number(id)))) {
          delete this.elementData[id]
          console.log("Removed element data for "+id)
        }
      }
    }
  },
  getters: {
    proficiency_bonus() {
      return CONSTANTS.prof_by_level[this.level] || 0
    },
    as_new_index() {
      for (let i = 0; i < 10000; i++) {
        if (this.noSave.characters[i] == null)
          return i        
      }
    }
  }
})
